@use "global" as *;

.footer-nav {
  display: flex;
  justify-content: space-between;
}

.footer-nav__left-content {
  width: 53.4%;

  @include mq(md) {
    width: 51.5%;
    display: flex;
  }
}

.footer-nav__left-content--width {
  width: 54.2%;
  @include mq(md) {
    width: 54.9%;
  }
}

.footer-nav__right-content {
  width: 46.6%;

  @include mq(md) {
    display: flex;
    justify-content: space-between;
    width: 48.5%;
  }
}

.footer-nav__right-content--width {
  width: 43.5%;
  @include mq(md) {
    width: 45%;
  }
}

.footer-nav__left-items:first-child {
  @include mq(md) {
    width: 51.4%;
  }
}

.footer-nav__left-items:nth-child(2) {
  margin-top: rem(2);
  @include mq(md) {
    width: 47%;
    margin-top: 0;
  }
}

.footer-nav__left-items--width {
  @include mq(md) {
    width: 46.8%;
  }
}

.footer-nav__right-items {
  @include mq(md) {
    width: 48.4%;
  }
}

.footer-nav__right-items--width:first-child {
  @include mq(md) {
    width: 60.4%;
  }
}

.footer-nav__right-items--width:nth-child(2) {
  @include mq(md) {
    width: 41.2%;
  }
}

.footer-nav__left-item,
.footer-nav__right-item {
  font-size: rem(16);
  color: $white;
}

.footer-nav__right-items:first-child .footer-nav__right-item:nth-child(2) {
  margin-top: rem(2);
}

.footer-nav__left-item--green,
.footer-nav__right-item--green {
  color: $green;
}

// コード_FB：ほとんど数値変わらないのでpadding統一してOK。連番は使わない
.footer-nav__left-link,
.footer-nav__right-link {
  position: relative;
  display: block;
  font-weight: $bold;
  // text-indent: rem(-18); コード_FB：マイナス系のものはあまり使わない方がよい ⇒ absoluteで対応
  padding: rem(17) 0 rem(15) rem(22);
  @include mq(md) {
    padding-top: rem(17);
  }
}

.footer-nav__right-items:nth-child(2) .footer-nav__right-item:nth-child(2) .footer-nav__right-link {
  line-height: 1.1;
  padding-top: rem(24);
}

.footer-nav__right-items:nth-child(2) .footer-nav__right-item:nth-child(3) .footer-nav__right-link {
  padding-top: rem(21);
}

.footer-nav__right-items:nth-child(2) .footer-nav__right-item:nth-child(4) .footer-nav__right-link {
  padding-top: rem(19);
}

.footer-nav__left-link::before,
.footer-nav__right-link::before {
  content: "";
  position: absolute; // コード_FB：アイコンの位置調整はabsoluteで行う
  top: rem(20);
  left: 0;
  display: inline-block;
  width: rem(18);
  height: rem(18);
  background-image: url(../images/common/starfish.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.footer-nav__left-link--icon-green::before,
.footer-nav__right-link--icon-green::before {
  background-image: url(../images/common/starfish-green-real.svg);
}

.footer-nav__right-items:nth-child(2) .footer-nav__right-item:nth-child(2) .footer-nav__right-link::before {
  top: rem(23);
}

.footer-nav__right-items:nth-child(2) .footer-nav__right-item:nth-child(3) .footer-nav__right-link::before {
  top: rem(24);
}

.footer-nav__right-items:nth-child(2) .footer-nav__right-item:nth-child(4) .footer-nav__right-link::before {
  top: rem(22);
}


.footer-nav__left-items:first-child .footer-nav__left-detail-items {
  @include mq(md) {
    position: relative;
    left: rem(-2);
  }
}

.footer-nav__left-detail-item,
.footer-nav__right-detail-item {
  font-weight: $normal;
}

.footer-nav__left-detail-item:nth-child(4),
.footer-nav__right-detail-item:nth-child(3) {
  @media (max-width: 767px) {
    font-size: 0.94rem;
  }
}

.footer-nav__left-detail-link,
.footer-nav__right-detail-link {
  display: block;
}

.footer-nav__left-detail-link {
  padding-left: rem(24);
}

.footer-nav__left-items:nth-child(2) .footer-nav__left-detail-link,
.footer-nav__right-detail-link {
  padding-left: rem(22);
}

.footer-nav__left-detail-item:first-child .footer-nav__left-detail-link,
.footer-nav__right-detail-item:first-child .footer-nav__right-detail-link {
  padding-top: rem(3);
}

.footer-nav__left-detail-item:not(:first-child) .footer-nav__left-detail-link {
  padding-top: rem(9);
}

.footer-nav__right-detail-item:not(:first-child) .footer-nav__right-detail-link {
  padding-top: rem(9);
}

.footer-nav__left-detail-item:not(:last-child) .footer-nav__left-detail-link,
.footer-nav__right-detail-item:not(:last-child) .footer-nav__right-detail-link {
  padding-bottom: rem(9);
}

.footer-nav__left-detail-item:last-child .footer-nav__left-detail-link,
.footer-nav__right-detail-item:last-child .footer-nav__right-detail-link {
  padding-bottom: rem(17);
}