@use "global" as *;

.site-map-mv {
  position: relative;
  height: rem(460);
  background-image: url(../images/common/site-map-mv.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  @include mq(md) {
    background-image: url(../images/common/site-map-mv-pc.webp);
    height: rem(548);
  }
}

.site-map-mv__title-text {
  margin-left: rem(2);
}

.site-map-mv--shadow-pc {
  @include mq(md) {
    box-shadow: 0 rem(4) rem(4) rgba($color: $black, $alpha: 25%);
  }
}