@use "global" as *;

.section-header {
  text-align: center;
}

.section-header__engtitle {
  font-family: $second-font-family;
  font-size: rem(32);
  font-weight: $normal;
  line-height: 1;

  @include mq(md) {
    font-size: rem(40);
  }
}

.section-header__engtitle::first-letter {
  color: $greenaccent;
}

.section-header__engtitle--white-pc {
  @include mq(md) {
    color: $white;
  }
}

.section-header__engtitle--white-pc::first-letter {
  @include mq(md) {
    color: $green2;
  }
}

.section-header__engtitle--voice-ls {
  letter-spacing: 0.01em;
}

.section-header__engtitle--large {
  font-size: rem(80);
  letter-spacing: 0.003em;
  margin-left: rem(-9);

  @include mq(md) {
    font-size: rem(80);
  }
}

.section-header__jatitle {
  font-size: rem(12);
  font-weight: $normal;
  color: $greenaccent;
  margin-top: rem(8);

  @include mq(md) {
    margin-top: rem(8);
  }
}

.section-header__jatitle--up {
  margin-top: rem(-6);
  padding-right: rem(1);

  @include mq(md) {
    margin-top: rem(-6);
    padding-right: rem(11);
  }
}

.section-header__jatitle--white-pc {
  @include mq(md) {
    color: $white;
  }
}

.section-header__title {
  font-size: rem(24);
  @include mq(md) {
    font-size: rem(32);
  }
}

.section-header__404-title {
  font-family: $third-font-family;
  font-size: rem(80);
  font-weight: $normal;
  line-height: 1;
  color: $white;
  letter-spacing: 0.5em;
  text-indent: 0.47em;
  @include mq(md) {
    font-size: rem(160);
    text-indent: 0.49em;
  }
}